import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
 import Row from 'react-bootstrap/Row';
 import Col from 'react-bootstrap/Col';
import 'bootstrap/dist/css/bootstrap.min.css';
import axios from 'axios';
import { toast } from 'react-toastify';
import { useState } from 'react';
import '../src/components/css/addproduct.css';
import {  useSelector } from "react-redux";

    
 
//  const user = useSelector((state) => state.userData.user);  



const firstproduct = {
    "name": '',
    "sku": '',
    "category": '',
    "quantity": '',
    "price": '',
    "description": '',
    "image": '',
    
  }
  const myapi = "https://ampacy-stock-api.onrender.com";






 
function AddProduct ()  {
       

   const userData =useSelector((state)=>state.userData.userData);



  


    


      


 console.log(userData);

    const [product, setProduct] = useState(firstproduct);
    const { name, sku,category,quantity,price ,description, image } = product;
  
    const inputChange = (e) => {
      const { name, value } = e.target;
      setProduct({ ...product, [name]: value });
    };
  
  
    console.log(product);
  
    const myData = {
      name,
      sku,
      category,
      quantity,
      price,
      description,
      image
  
    };
  



      
  
       const submithandle = async (e) => {



        
   if (userData) {
    submithandle (true);
  } else {
    submithandle (false);
  }
      e.preventDefault();
  
             console.log(myData);
  
      
    
  
      try {
        const response = await axios.post(`${myapi}/api/products
            `, myData, {
          withCredentials: true,
        }); toast.success("Product add succusfully");
          




  
  
        return response.data;
      } catch (error) {
        // we get all type of error
        const message =
          (error.response && error.response.data && error.response.data.message) ||
          error.message ||
          error.toString();
        toast.error(message);
        console.log(message)
      }
    }  ;

       

    return(
    <>
  <Container fluid>
    <Row>
    <Col sm={7}> 
     
       <form onSubmit={submithandle}>
            <div className="forminputs">
              <h3 className="adproduct">Add product</h3>
              <label className="loginid">Name</label>
              <input type='text'
                required value={name}
                name="name"
                onChange={inputChange}
                placeholder='Enter your Name'
                className="inputmail"
              />

              <label className="loginid">sku </label>
              <input
                type='text'
                required value={sku}
                name="sku"
                onChange={inputChange}
                placeholder='Enter your email'
                className="inputmail"
              />
              <label className="loginid">category</label>
              <input
                type='text'
                required
                value={category}
                name="category"
                onChange={inputChange}
                placeholder='category,'
                className="inputmail"
              />
              <label className="loginid">quantity</label>
              <input
                type='number'
                required
                value={quantity}
                name="quantity"
                onChange={inputChange}
                placeholder='quantity'
                className="inputmail"
              />
               <label className="loginid">price</label>
              <input
                type='number'
                required
                value={price}
                name="price"
                onChange={inputChange}
                placeholder='price'
                className="inputmail"
              />
               <label className="loginid">description</label>
              <input
                type='text'
                required
                value={description}
                name="description"
                onChange={inputChange}
                placeholder='description'
                className="inputmail"
              />
               <label className="loginid">image</label>
              <input
                type='text'
                required
                value={image}
                name="image"
                onChange={inputChange}
                placeholder='image'
                className="inputmail"
              />
            </div>




           
            <div className="formbtn">
             
              <Button type="submit" variant="primary" className="btn-submit">
                Submit
              </Button>

            </div>
         



        </form>

        </Col>
        <Col sm={5}>

          <h1>hi</h1>
        </Col>
      
        </Row>
       
  </Container>
    
    
    </>

    )
    
}

export default AddProduct ;