import Home from './route';
import Dashboad from './Dashboad';
import Forget from './components/forgetpassword';
import AddProduct from './addprodect';
import { BrowserRouter } from 'react-router-dom';
import { Routes, Route } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';
import { useEffect } from 'react';

import axios from 'axios';

import { useDispatch } from 'react-redux';
import { SET_LOGIN } from './redux/Slice';
const baseUrl = "https://ampacy-stock-api.onrender.com";


export const getLoginStatus = async () => {
    

    try {
        const response = await axios.get(`${baseUrl}/api/users/loggedin
        `,
        
        ); toast.success("hi");
       
       
    
         if (response.statusText === "OK") {
           console.log('correct');
         }
            console.log(response.data);
        return response.data; 
      }
    
    catch (error) {
        const message =
            (error.response && error.response.data && error.response.data.message) ||
            error.message ||
            error.toString();
        toast.error(message);
    }
}




function App() {
    
    const dispatch = useDispatch();

    useEffect(() => {
        const status = getLoginStatus();
        dispatch(SET_LOGIN(status))
    })

    return (

        <>
            <ToastContainer />
            <BrowserRouter>
                <Routes>


                    <Route path="/" element={<Home />} />
                    <Route path="Dashboad" element={<Dashboad />} />
                    <Route path="Forget" element={<Forget />} />
                    <Route path="AddProduct" element={<AddProduct />} />
                    

                </Routes>



            </BrowserRouter>
        </>
    )
}

export default App;