import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import 'bootstrap/dist/css/bootstrap.min.css';
import logo from './image/nav/logos.png';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { useState } from 'react';
import './css/navbar.css';
import { Link } from "react-router-dom";
import axios from 'axios';
import { toast } from 'react-toastify';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { useNavigate } from "react-router-dom";
import About from '../Dashboad';
import { useDispatch } from "react-redux";
import { SET_LOGIN,SET_NAME} from "../redux/Slice";

import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';

const baseUrl = "https://ampacy-stock-api.onrender.com";
const registerapi = 'https://ampacy-stock-api.onrender.com/api/users/register';



const firstValue = {
  "email": '',
  "password": '',
}


function Nav({logstaus}) {
  const navigate = useNavigate();
  const dispatch = useDispatch();


  //login modal
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  //register modal
  const [reg, setReg] = useState(false);
  const handleCloses = () => setReg(false);
  const handleShows = () => setReg(true);


  //form API usestate

  const [data, setData] = useState(firstValue);
  const { email, password } = data;



  //forget password

  const inputChange = (e) => {
    const { name, value } = e.target;
    setData({ ...data, [name]: value });
    // localStorage.setItem('data', JSON.stringify(data));
  };


  const submithandle = async (e) => {
    e.preventDefault();

    const myData = {
      email,
      password,
    };

    // localStorage.setItem("e", myData.email);
    // localStorage.setItem("p", myData.password);
    
   
    try {
      const response = await axios.post(`${baseUrl}/api/users/login`, myData, {
        withCredentials: true,
      }); toast.success("User login  Successfully");
      
      await dispatch(SET_LOGIN(true));
      await dispatch(SET_NAME(data.name));
     
      navigate("/Dashboad")
      
     
       if (response.statusText === "OK") {
         console.log('correct');
       }
          console.log(response.data);
          <About lokesh={ response.data } />
         

      return response.data;
        
      
    } catch (error) {
      // we get all type of error
      const message =
        (error.response && error.response.data && error.response.data.message) ||
        error.message ||
        error.toString();
      toast.error(message);
      console.log(message)
      dispatch(SET_LOGIN(false));
    }
  
  }


                  // api method 2 register

                  const [a,setName]=useState("");
                  const[b,setEmail]=useState("");
                  const[c,setPassword]=useState("");
                  const[d,setPhone]=useState("");    


           const submithandles =(e)=>{
            e.preventDefault();
           
                  const Exmpl = {  
                    name: a,
                    email: b,
                    password: c,
                    phone: d,
                }


                        // local storage //
                localStorage.setItem("e", Exmpl.name);

                  axios.post(registerapi, Exmpl)
                  .then(response => {
                      console.log('Response:', response.data);
                      toast.success("User Register  Successfully");
                  })
                  
                  .catch(error => {
                      console.error('Error:', error.response);
                  });
                };


  return (
    <>
        <Container fluid>
        <Row>
          <Col sm={3}>
            <img src={logo} className="logo" alt="navimg" />

          </Col>
          <Col sm={6}>
            <div className="list">
              <ul className="ullist">
                    <li>Home <KeyboardArrowDownIcon /></li>
                    <li>Listings<KeyboardArrowDownIcon /></li>
                    <li>Blogs<KeyboardArrowDownIcon /></li>
                    <li>Pages<KeyboardArrowDownIcon /></li>
                    <li>Shop<KeyboardArrowDownIcon /></li>
                    <li>Contact us<KeyboardArrowDownIcon /></li>
               

              </ul>

            </div>

          </Col>
          <Col sm={3}>
            <div className="reglog">
              <p onClick={handleShows} className="reg">Register</p>
              <button onClick={handleShow} className="loginbtn">Login</button>
            </div>

          </Col>

        </Row>
        {/* login */}
        <Modal show={show} onHide={handleClose} animation={false} size="md" centered className="formmodel">
          <div>
         

          </div>


          <h2 className="txtlogin">LOGIN</h2>
          <form onSubmit={submithandle}>
            <div className="forminputs">
              <label className="loginid">LOGIN ID</label>
              <input type='gmail'
                placeholder='Enter your Email'
                required value={email}
                name="email"
                onChange={inputChange}
                className="inputmail"
              />
              <label className="loginid">Password</label>
              <input
                type='password'
                placeholder='password'
                required value={password}
                name="password"
                onChange={inputChange}
                className="inputmail"

              />
            </div>



            <div className="forget">
              <label for="" className="Remember"><input type="checkbox"  value="required"/> Remember Me</label>
              <Link to="/Forget" className="submit forpsw">Forget password</Link>

            </div>
            <div className="Rgister">
              <p className="logreg">Don't have a account<a href="@" className="regs">Register</a></p>

            </div>

            <div className="formbtn">
              <Button variant="secondary" onClick={handleClose} className="btncancel">
                Cancel
              </Button>
              <Button type="submit" variant="primary" className="btnsubmit">
                submit
              </Button>

            </div>
          </form>
        </Modal>

      </Container>
      <Container>
        {/* register */}
        <Modal show={reg} onHide={handleClose} animation={false} size="md" centered className="formmodel">


          <h2 className="txtlogin">REGISTER</h2>
          <form onSubmit={submithandles}>
            <div className="forminputs">
              <label className="loginid">Name</label>
              <input type='text'
                required value={a}
                name="a"
                // onChange={inputChange1}
                onChange={e => setName(e.target.value)}
                placeholder='Enter your Name'
                className="inputmail"
              />

              <label className="loginid">E-mail </label>
              <input
                type='email'
                required value={b}
                name="b"
                // onChange={inputChange1}
                onChange={e => setEmail(e.target.value)}
                placeholder='Enter your email'
                className="inputmail"
              />
              <label className="loginid">Password</label>
              <input
                type='password'
                required
                value={c}
                name="c"
                // onChange={inputChange1}
                onChange={e => setPassword(e.target.value)}
                placeholder='password'
                className="inputmail"
              />
              <label className="loginid">Mobile number</label>
              <input
                type='number'
                required
                value={d}
                name="d"
                // onChange={inputChange1}
                onChange={e => setPhone(e.target.value)}
                placeholder='confirm your password'
                className="inputmail"
              />
            </div>




            <div className="Rgister">
              <p className="reglogs">All redy register kindly <a href="@">LOGIN</a></p>

            </div>

            <div className="formbtn">
              <Button variant="secondary" onClick={handleCloses} className="btncancel">
                Cancel
              </Button>
              <Button type="submit" variant="primary" className="btnsubmit">
                Submit
              </Button>

            </div>
          </form>

        </Modal>
      </Container>

      <ToastContainer />
      <div className="preloader"></div>
    </>
   )
 }

export default Nav;




