import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';

import 'bootstrap/dist/css/bootstrap.min.css';
import './components/css/fruits.css';

import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import { useDispatch,useSelector } from "react-redux";
import { SET_LOGIN} from "./redux/Slice";


  

//import '../src/components/css/preloader.css'


function Dashboad (){

   const navigate = useNavigate();
   const dispatch = useDispatch();

   const user = useSelector(SET_LOGIN);

   const remove = () => {

      dispatch(SET_LOGIN(false));
      // localStorage.removeItem('p');
      localStorage.removeItem('e');
      navigate("/")
   
   }
    

    return(

        <>
         
       <Navbar bg="dark" data-bs-theme="dark">
        <Container>
             <Navbar.Brand href="#home">Navbar</Navbar.Brand>
             <Nav className="me-auto">
            <Nav.Link href="#home">Home</Nav.Link>
            <Nav.Link href="#features">Features</Nav.Link>
            <Nav.Link href="#pricing">Pricing</Nav.Link>
            <Link  to="/AddProduct" className="addproduct">Add product</Link>
            <Nav.Link href="#loginStatus">
            {/* {localStorage.getItem('e') && (
           
                <p> welcome  {localStorage.getItem('e')}</p>
              )} 
      <Nav.Link href="#logout"> <button onClick={remove}>logout</button></Nav.Link>
      */}
             {
                                (user ?

                                    (<Link to="/" onClick={remove}>Logout</Link>)
                                    :
                                    (<Link to="/"> Login</Link>))

                            }

            </Nav.Link>

          </Nav>
        </Container>
      </Navbar>
     <Container mt-5>
     <h6>hi this is about page</h6>

     </Container>
      
             
        </>
    )
}

export default Dashboad;



