
import './css/navbar.css';
import { useNavigate } from "react-router-dom";
import { Link } from "react-router-dom";


import 'bootstrap/dist/css/bootstrap.min.css';
// import Modal from 'react-bootstrap/Modal';
import { useState } from "react";
import Button from 'react-bootstrap/Button';

import { toast } from 'react-toastify';
import { ToastContainer } from 'react-toastify';
import axios from 'axios';
// import { useNavigate } from "react-router-dom";

const myapi = "https://ampacy-stock-api.onrender.com";

const firstValue = {
  "email": '',

}


function Forget() {
  const navigate = useNavigate();
  // const navigate =useNavigate();
  //modal state
  // const [show, setShow] = useState(false);
  // const handleClose = () => setShow(false);


  //api 



  const [data, setData] = useState(firstValue);
  const { email } = data;
  const inputChange = (e) => {
    const { name, value } = e.target;
    setData({ ...data, [name]: value });
  };
  const submitmail = async (e) => {
    e.preventDefault();

    const myData = {
      email,

    };
    console.log(myData);
    try {
      const response = await axios.post(`${myapi}/api/users/forgotpassword
        `, myData, {
        withCredentials: true,
      }); toast.success("password reset succusfully");
      navigate("/")

      // if (response.statusText === "OK") {
      //   console.log('correct');
      // }
      return response.data;
    } catch (error) {
      // we get all type of error
      const message =
        (error.response && error.response.data && error.response.data.message) ||
        error.message ||
        error.toString();
      toast.error(message);
      console.log(message)
    }
    console.log(data)

  };



  return (
    <>




      <div className="forgetcard">

        <h2 className="txtlogin">Forget password</h2>
        <form onSubmit={submitmail}>
          
          <div className="forminputs">
            <label className="loginid">USER ID</label>
            <input type='email'
              placeholder='Enter your Email'
              required value={email}
              name="email"
              onChange={inputChange}
              className="inputmail"
            />

          </div>





          <div className="formbtn">
          
          <Link to="/">
          <Button type="reset" variant="secondary" className="forgetreset">Cancel</Button>
            
            
            </Link>
            <Button type="submit" variant="primary" className="forgetsubmit">
              submit
            </Button>

          </div>
        </form>
      </div>



      <ToastContainer />
    </>

  )

}

export default Forget;

